	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Unpacked and in the Rack","date":"16 Sep 2005","slug":"/2005-09-16-unpacked-and-in-the-rack/","tags":"Y2005"},"html":"<p><img src=\"http://www.papascott.de/fotos//4dells.JPG\" border=\"0\" height=\"240\" width=\"320\" alt=\"4dells.JPG\" /></p>\n<p>Just so Nico knows that I take good care of the <a href=\"http://lumma.de/eintrag.php?id=2047\" title=\"Hochkantserver [Lummaland - das Weblog]\">toys</a> he buys for me. The bottom two are a week old, the top two are new today. (The locks are just for show, the keys are all in the glove compartment.)</p>\n<p>This is my first experience with Dell servers. They are nice and easy to install, I'll take more anytime.</p>"},"next":{"fields":{"title":"Round Numbers","date":"16 Sep 2005","slug":"/2005-09-16-round-numbers/"}},"previous":{"fields":{"title":"McCafe Elmshorn","date":"17 Sep 2005","slug":"/2005-09-17-mccafe-elmshorn/"}}},{"node":{"fields":{"title":"Round Numbers","date":"16 Sep 2005","slug":"/2005-09-16-round-numbers/","tags":"Y2005"},"html":"<p>The head of Der Spiegel's Berlin bureau sums up Germany's budget dilemma in the <a href=\"http://service.spiegel.de/cache/international/0,1518,374770,00.html\" title=\"Wall Street Journal: Say it Slowly: 'Zukunftsangst' - International - SPIEGEL ONLINE - News\">Wall Street Journal: Say it Slowly: 'Zukunftsangst'</a>. </p>\n<blockquote><p>A glance at the state's finances shows how dramatic the situation is: Of 190 billion euros in tax revenues, 80 billion is passed on to the cash-strapped state pension system, 30 billion goes to the unemployed, and another 40 billion belongs to the banks, just to service debt. The rest is not even enough to pay the bureaucracy and to build roads. Ever-new credits are constantly needed so that Germany at least has the appearance of a prosperous country.</p></blockquote>\n<p>Of course the politicians never put it this directly, and the press only very rarely. It reminds me of the quietest room in hell. Everyone stands in a vat of raw sewage up to the lower lip, and the only sound is a mass whisper. \"Don't make waves. Don't make waves....\"<a href=\"http://vowe.net/archives/006327.html\" title=\"vowe dot net :: Haben Sie Angst?\">(via vowe)</a></p>"},"next":{"fields":{"title":"Tiger in your Tank","date":"15 Sep 2005","slug":"/2005-09-15-tiger-in-your-tank/"}},"previous":{"fields":{"title":"Unpacked and in the Rack","date":"16 Sep 2005","slug":"/2005-09-16-unpacked-and-in-the-rack/"}}},{"node":{"fields":{"title":"Tiger in your Tank","date":"15 Sep 2005","slug":"/2005-09-15-tiger-in-your-tank/","tags":"Y2005"},"html":"<p><a href=\"http://www.cnn.com/2005/WORLD/europe/09/14/germany.catfuel.reut/index.html\" title=\"CNN.com - Inventor denies dead cat fuel story\">CNN.com - Inventor denies dead cat fuel story:</a> \"A German inventor said he has developed a method to produce crude oil products from waste that he believes can be an answer to the soaring costs of fuel, but denied a German newspaper story implying he also used dead cats.\" Good thing I read <a href=\"http://www.bildblog.de/?p=797\">BildBlog</a>, so I already knew that you can't make bio-diesel from cats. But maybe that's why our cat leaves so many dead mice on our patio... they're meant to power my <a href=\"http://www.papascott.de/archives/2004/08/21/used-car/\">Lupo</a>!</p>"},"next":{"fields":{"title":"Christopher Explains the Solar System","date":"15 Sep 2005","slug":"/2005-09-15-christopher-explains-the-solar-system/"}},"previous":{"fields":{"title":"Round Numbers","date":"16 Sep 2005","slug":"/2005-09-16-round-numbers/"}}}],"pathPrefix":"","first":false,"last":false,"index":282,"pageCount":1101}}

